:root {
  --bright: #e9f2ea;
  --light-blue: ##2ab0bf;
  --blue: #0367a6;
  --navy: #051940;
  --dark: #020e26;
  font-size: 16px;
}

body {
  background-color: var(--bright);
  color: var(--navy);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
}

h4 {
  text-transform: uppercase;
  margin-bottom: 5px;
  color: --var(--dark);
  font-size: 1.1rem;
}

button {
  margin-bottom: 10px;
}

select,
input {
  width: 150px;
  font-size: 1rem;
  vertical-align: middle;
}

label {
  font-weight: 300;
  font-size: 1rem;
  vertical-align: middle;
}

.layout {
  display: flex;
}

.inputs {
  width: 100%;
}

.canvas {
  box-sizing: border-box;
}

.offscreen-canvas {
  display: none;
}

@media only screen and (min-width: 901px) {
  body {
    overflow: hidden;
  }

  .layout {
    flex-direction: row;
  }

  .inputs {
    height: 100vh;
    padding: 1vh;
  }

  .inputs-body {
    display: block;
    overflow: hidden scroll;
    height: 100%;
  }

  .canvas {
    width: 100vh;
    height: 100vh;
    padding: 1vh;
  }
}

@media only screen and (max-width: 900px) {
  :root {
    font-size: 20px;
  }

  .layout {
    flex-direction: column;
  }

  .inputs {
    padding: 2vw;
  }

  .canvas {
    background-color: var(--bright);
    width: 100vw;
    height: 100vw;
    padding: 2vw;
    position: sticky;
    top: 0;
  }
}
